body{
    background-color: #31081f;
    color: #f7f0f5;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
    
}

.navbar{
    position: fixed;
}
.navbar, .estoutriste{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: row;
    height: 80px;
    
   
    
    /* background-color: coral; */
}


.navbar-item:hover, .estoutriste:hover{
    color: #f7f0f5;
    background: #441932;

}
.estoutriste{
    color: #f7f0f5;
    height: 0px;

    
}

.space{
    flex:4;
}
.navbar-item, .estoutriste{
    flex: 1;
    text-align: center;
    font-size: 1em;
    background: #a54657;
    text-decoration: none;

}

