.sobre-mim-container{
    height: 100vh;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.foto-container{
    display: flex;
    float: left;
    
}

.foto-perfil{
    flex: 1;
    object-fit: contain;
    height: 300px;
    border-radius: 200px;
    border: 6px solid #ee4266;   

} 
.foto-perfil:hover{
    border: 6px solid #6d2f53;   
}

.aboutme{
    font-size: 2em;
    text-align: left;
    text-transform: uppercase;
    color: #DDA77B; 
    
}

.descricao{
    max-width: 800px;
    text-align: justify
}
